export const formatted = districts.map((item: any) => {
    item.localgovs = localgovs[item.name]
    return item;
})


export const pradeshes = [" . ", " . ", " . ", " . ", " . ", " . ", " . "];
export function getPradeshData() {
    let data: any = {};
    pradeshes.forEach((item, index) => {
        data[item] = { districts: formatted.filter((item: any) => item.pradesh_id === index + 1) }
    })
    return data;
}

